import { IonTabs, IonTabBar, IonTabButton, IonIcon, IonLabel, IonBadge, IonRouterOutlet } from '@ionic/react';
import { IonReactRouter } from '@ionic/react-router';
import { square, ellipse, homeSharp } from 'ionicons/icons';

import { Redirect, Route } from 'react-router';
import Profile from './Profile';


var Tab2 = "12hdqjhwaerwqr1324";
var Tab3 = "12gdwhjhjkekwjt1324";
function Main() {
    return <IonReactRouter >
        <IonTabs>
            <IonRouterOutlet>
                <Route path="/main" component={Profile} exact={true} />
                <Route path="/tab2" component={Tab2} exact={true} />
                <Route path="/tab3" component={Tab3} />
            </IonRouterOutlet>
            <IonTabBar slot="bottom">
                <IonTabButton tab="tab1" href="/main" className="tab-button">
                    <IonIcon icon={homeSharp} />
                    <IonLabel>Home</IonLabel>
                </IonTabButton>
                <IonTabButton tab="tab2" href="/tab2">
                    <IonIcon icon={ellipse} />
                    <IonLabel>Tab 2</IonLabel>
                </IonTabButton>
                <IonTabButton tab="tab3" href="/tab3">
                    <IonIcon icon={square} />
                    <IonLabel>Tab 3</IonLabel>
                </IonTabButton>

                <IonTabButton tab="tab3" href="/tab3">
                    <IonIcon icon={square} />
                    <IonLabel>Tab 3</IonLabel>
                </IonTabButton>

            </IonTabBar>
        </IonTabs>
    </IonReactRouter >
}

export default Main;