import { IonApp, IonButton, IonCol, IonInput, IonItem, IonLabel, IonRow } from "@ionic/react";

function Contact() {
    return <IonApp>
        <IonRow>
            <IonCol size="12">

                <IonLabel>
                    <b>Enter your number</b>
                </IonLabel>
            </IonCol>
            <IonCol size="12">
                <IonLabel style={{ color: "grey" }}>
                    We'll send a verification code in your current phone
                 </IonLabel>
            </IonCol>
            <IonCol>
                <form action="">
                    <IonItem className="inp">
                        <IonInput placeholder="Phone Number" autofocus={true} type="number" ></IonInput>
                    </IonItem>
                </form>
            </IonCol>
            <IonCol size="12">
                <IonLabel>
                    <small style={{ color: "red" }}>Invalid phone number  </small>
                </IonLabel>
            </IonCol>
        </IonRow>
        <div style={{ paddingBottom: "20px" }}>
            <IonButton size="large" expand="block" className="rounda" routerLink="/otp">NEXT</IonButton>
            <footer style={{ color: "grey", textAlign: "center" }}><small>&copy;2021 Laundree</small></footer>
        </div>
    </IonApp>
}

export default Contact;