import icon from "../assert/icon5.svg"
import './Landing.css'
import { IonApp, IonImg, IonLabel } from '@ionic/react';
function Landing() {
    return <IonApp className={"bg"}>
        <div className="landing-page-logo">
            <IonImg src={icon} className="logo" />
            <label>Neat ‘n Tidy</label>
        </div>
    </IonApp>

}
export default Landing;