import { IonApp, IonButton, IonCol, IonIcon, IonInput, IonItem, IonLabel, IonRow } from '@ionic/react';
import { arrowBack } from 'ionicons/icons';
import { Link } from 'react-router-dom';

function Otp() {

    return <IonApp>
        <IonRow>
            <IonCol size="12">
                <IonButton routerLink="/contact" className="tranparent-button" >
                    <IonIcon icon={arrowBack} routerLink="/" />
                </IonButton>

            </IonCol>
            <IonCol size="12">

                <IonLabel>
                    <b>Enter your Verification Code</b>
                </IonLabel>
            </IonCol>
            <IonCol size="12">
                <IonLabel style={{ color: "grey" }}>
                    Your verification code was sent !. Wait a moment...
               </IonLabel>
            </IonCol>

            <IonCol size="12">
                <IonLabel>
                    <small style={{ color: "red" }}>Change your Contact - 0712345678 ?  </small><Link to="/">change</Link>
                </IonLabel>
            </IonCol>
            <IonCol>
                <IonItem className="inp">
                    <IonInput placeholder="Verificaton Code"  ></IonInput>
                </IonItem>
            </IonCol>
        </IonRow>
        <div style={{ paddingBottom: "20px" }}>
            <IonButton size="large" expand="block" className="rounda" routerLink="/main">Confirm</IonButton>
            <footer style={{ color: "grey", textAlign: "center" }}><small>&copy;2021 Laundree</small></footer>
        </div>
    </IonApp>

}
export default Otp;